import { sql } from '@vercel/postgres';
import { Alert, Employee } from './types';

// Mock Data for Fallback
let MOCK_ALERTS: Alert[] = [];
let MOCK_ASSIGNMENTS: any[] = [];

// Helper to handle both DB and Mock modes seamlessly
export async function fetchAlerts(): Promise<Alert[]> {
    try {
        // Attempt real DB call
        // Table: alerts (id, factory, timestamp, violation_type, assigned_employee_id, status)
        const { rows } = await sql`SELECT * FROM alerts ORDER BY timestamp DESC LIMIT 50`;
        return rows.map(r => ({
            id: r.id,
            factory: r.factory,
            timestamp: r.timestamp,
            violationType: r.violation_type,
            assignedEmployeeId: r.assigned_employee_id,
            status: r.status
        })) as Alert[];
    } catch (error) {
        console.warn("Database connection failed (expected during build/without env vars). Using In-Memory Mock.");
        return MOCK_ALERTS;
    }
}

export async function createAlertInDb(alert: Alert) {
    try {
        await sql`
      INSERT INTO alerts (id, factory, timestamp, violation_type, assigned_employee_id, status)
      VALUES (${alert.id}, ${alert.factory}, ${alert.timestamp}, ${alert.violationType}, ${alert.assignedEmployeeId}, ${alert.status})
    `;
    } catch (error) {
        console.warn("DB Write failed, pushing to mock store.");
        MOCK_ALERTS.unshift(alert);
        if (MOCK_ALERTS.length > 50) MOCK_ALERTS.pop();
    }
}

export async function updateAlertStatusInDb(id: string, status: 'Resolved' | 'Active') {
    try {
        await sql`UPDATE alerts SET status=${status} WHERE id=${id}`;
    } catch (error) {
        const idx = MOCK_ALERTS.findIndex(a => a.id === id);
        if (idx !== -1) MOCK_ALERTS[idx].status = status;
    }
}

